import inputContact from '../components/addressCheck/inputContact.vue'

export default{
	components: {
		inputContact
	},
	data() {
		return {
			pageIndex: 1,
			pageDatas: [],
			
			currentIndex: '',
			location:{
				contactor:'',
				contactPhoneNumber:'',
				
				id : 0,
				province:'',
				city:'',
				district:'',
				address:'',
				fullAddress:'',
				longitude:'',
				latitude:''
			},
			
			addressType:'',
			loadingText: "正在加载....",

		}
	},
	methods:{
		addressPageQuery(){
			this.checkLogin().then(val => {
				if(val === true){
					this.https({
						url: this.interfaces.initAddress,  
						method: 'POST',
						data: {
							'addressType': this.addressType,
							'pageIndex':this.pageIndex
						},
						header: {
							'content-type': 'application/json'
						},
						success: ((res) => { 
							if(res.data.length <= 0){
								if(this.pageIndex == 1){
									this.pageDatas = res.data;
								}
								this.pageIndex --;
								this.loadingText = "没有更多";
							}else{
								if(this.pageIndex == 1){
									this.pageDatas = res.data;
								}else{
									this.pageDatas = this.pageDatas.concat(res.data);	
								}
								//获取发布内容
								this.loadingText = "上拉获取更多";
							}
						})
					}); 
				}
			});
		},
		updataAddress(e){
			var index = e.currentTarget.id;
		
			const locationData = this.pageDatas[index];
			
			this.currentIndex = index;
			this.location = {
				contactor:locationData.name,
				contactPhoneNumber:locationData.telephone,
				
				id :locationData.id,
				province:locationData.province,
				city:locationData.city,
				district:locationData.county,
				address:locationData.address,
				fullAddress:locationData.fullAddress,
				longitude:locationData.longitude,
				latitude:locationData.latitude
			};
			
			let _this = this;
			
			//监听子页面数据
			_this.$eventHub.$on('fire',function(data){
				_this.location.id = data.id;
				_this.location.contactor = data.name;
				_this.location.contactPhoneNumber = data.phoneNumber;
				_this.location.province = data.province;
				_this.location.city = data.city;
				_this.location.district = data.district;
				_this.location.address = data.locationAddress;
				_this.location.fullAddress = data.locationFullAddress;
				_this.location.longitude = data.longitude;
				_this.location.latitude = data.latitude;
					
				setTimeout(() => {
					//弹出联系人姓名 电话框
					_this.contactOption();
				},1000)
				
				
				//清除监听
				_this.$eventHub.$off('fire');
			});
			
			uni.navigateTo({
			 	url:'../components/searchHeader/publishMap?myAddressType='+_this.addressType+'&allowEdit=true'+
				'&id='+_this.location.id+'&name='+_this.location.contactor+'&phoneNumber='+_this.location.contactPhoneNumber+
				'&province='+_this.location.province+'&city='+_this.location.city+'&district='+_this.location.district+'&locationAddress='+_this.location.address+
				'&locationFullAddress='+_this.location.fullAddress+'&longitude='+_this.location.longitude+'&latitude='+_this.location.latitude,
			 	success: res => {},
			 	fail: () => {},
			 	complete: () => {
			 		
				}
			});	
		},
		deleteAddress(e){
			if(this.checkLogin == false){
				return
			}
			var index = e.currentTarget.id;
			
			const locationData = this.pageDatas[index];
			
			uni.showModal({
				title: '地址删除确认',
				content: '确定要删除该地址,删除后将不可恢复？',
				cancelText: '否',
				confirmText: '是',
				success: res => {
					if (res.confirm) {
						this.https({
							url: this.interfaces.deleteAddress,  //POST json传输必须未转字符串
							method: 'POST',
							data: {
								'addressType':this.addressType, 
								'addressId':locationData.id
							},
							header: {
								'content-type': 'application/json'
							},
							success: ((res) => { 
								if(res.data === true){
									//重新刷新下
									this.pageIndex = 1;
									this.addressPageQuery(); 
								}
							})
						}); 
					}
				}
			})           
		},
		contactOption(){
			this.$refs.inputContact.show();
		},
		contactConfirmCallback(data){
			let ads=data.inputInfo;
			
			this.location.contactor = ads.contactor;
			this.location.contactPhoneNumber = ads.contactPhoneNumber;
			
			this.updateAddress();
			
		},
		updateAddress(){
			//获取修改的数据
			const addressData = this.pageDatas[this.currentIndex];
			console.log(addressData)
			
			const contactData = {
				addressType: this.addressType,
				contactor:this.location.contactor,
				contactPhoneNumber:this.location.contactPhoneNumber,
				id : this.location.id,	
				province:this.location.province,
				city:this.location.city,
				district:this.location.district,
				address:this.location.address,
				fullAddress:this.location.fullAddress,
				longitude:this.location.longitude,
				latitude:this.location.latitude,
			};
						
			this.https({
				url: this.interfaces.updateContact,  //POST json传输必须未转字符串
				method: 'POST',
				data: contactData,
				header: {
					'content-type': 'application/json'
				},
				success: ((res) => { 
					this.location.id = res.data;
					
					addressData.name = this.location.contactor;
					addressData.telephone = this.location.contactPhoneNumber;
					addressData.fullAddress = this.location.fullAddress;
				})
			}); 
		},
		async checkLogin(){
			//获取同步获取当前storeage的相关信息
			let fromUrl = "/pages/user/userAddress";
			let toUrl = ""; 
			let authFinishedToUrl = '/pages/tabBar/home/home'; //授权完成后跳转页面
			let refuseUrl = '/pages/tabBar/home/home';
			let authUrl = "/pages/auth/login";
			let tokenVerify = true;
			return await this.login.checkLogin(fromUrl,toUrl,refuseUrl,authUrl,tokenVerify,authFinishedToUrl);
		}
	},
	onLoad(options) {
		console.log('The page is be onLoad');
		uni.showLoading({title:"加载中..."})
		
		this.pageIndex = 1;
		this.addressType = options.addressType;
		if(this.addressType === 'shiper'){
			uni.setNavigationBarTitle({
			    title:"我的站点地址"
			});
		}else if(this.addressType === 'receiver'){
			uni.setNavigationBarTitle({
			    title:"我的收货地址"
			});
		}else{
			uni.setNavigationBarTitle({
			    title:"我的地址"
			});
		}
		
		this.addressPageQuery(); 
	},
	onPullDownRefresh(){
		uni.showLoading({title:"加载中..."})
		setTimeout(() => {
			this.pageIndex = 1;
			this.pageDatas = [];
			this.loadingText = "加载中...";
			this.addressPageQuery();
			uni.stopPullDownRefresh();
		},1000)
	},
	// 上拉加载
	onReachBottom(){
		this.pageIndex++;
		this.addressPageQuery();
		console.log('start onReachBottom');
	},
	
}			
